
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: { XHeader, GroupTitle, Flexbox, FlexboxItem, XButton },
data() {
return {

partyBranch:'',
name1:'',
departmentname:'',
totalscore:'',
pointdetail:'',


}
},
filters: {

formatDuring: function (value) {

Date.prototype.toLocaleString = function(){
return this.getFullYear() +'年'+ (this.getMonth()+1)+'月'+this.getDate()+'日'+this.getHours()+'时'+this.getMinutes()+'分'
}
return new Date(value).toLocaleString();
}
,
Upper: function (value) {
return value> 0 ? '+' + value : value;
}
},

methods: {
getUser1() {
axios.get('ppartymember/queryByUserId', {
params: {
userid:this.$route.params.userid

}
}) .then(res => {
console.log(res.data)
this.partyBranch=decodeURIComponent(this.$route.params.partyBranch)

this.totalscore=res.data.totalscore;
this.name1=res.data.name;
this.departmentname=res.data.departmentname
this.departmentid=res.data.departmentid

})
.catch(err => {
console.log(err);
});
},

/*  getParams() {


axios({
method: 'get',
url: 'pdepartment/queryById',
params: {
departmentid:this.departmentid

}
}) .then((res)=> {
this.partyBranch=res.data.partyBranch;


console.log(res.data)
}).catch(function (error) {
console.log(error);
})
},*/
getPoint() {

axios({
method: 'get',
url: 'pscoreparty/queryByUserId',
params: {
userid:this.$route.params.userid,
year:new Date().getFullYear()
}
}) .then((res)=> {
this.pointdetail=res.data



}).catch(function (error) {
console.log(error);
})




}
},mounted() {
/*
this.getPoint();*/
this.getUser1();
this.getPoint()
/*this.getParams();*/
},


};



